import axios from 'axios';
import Promise from 'promise';
import Qs from 'qs';

axios.defaults.withCredentials = true;

function request(opt, resolve, reject) {
	let method = opt.method || 'get';
	if (opt.localData) {
		window.setTimeout(function () {
			resolve(opt.localData);
		}, 1000);
	} else {
		let params = null;
		// 处理get或post请求的参数形式
		if (method === 'get') {
			params = {params: opt.data || {}};
		} else {
		    if (!opt.headers){
                params = [];
                let d = opt.data;
                for (let o in d) {
                    params.push(o + "=" + d[o]);
                }
                params = params.join("&");
            }else {
                params = opt.data
			}
			
			if(opt.paramsSerializer){
				params = Qs.stringify(opt.data, {arrayFormat: 'indices',allowDots: true}) 	
				console.log(params)
			}
		}
		// 处理路径参数形式
		if (opt.urlPath && opt.urlPath.length > 0) {
			opt.url += "/" + opt.urlPath.join("/");
		}

		axios[method](opt.url, params,opt.headers).then(function (response) {
            if (opt.headers && !response.data.errno){
                resolve(response.data);
            }
			if (response.data.errno === 0) {
				resolve(response.data);
			} else {
				reject(response.data);
			}
		}).catch(function (response) {
			reject(response);
		});
	}
}

export default function (opt) {
	return new Promise(function (resolve, reject) {
		if (opt instanceof Array) {

		} else {
			request(opt, resolve, reject);
		}
	});
};



