<template>
    <element>
        <el-row>
            <el-col :span="24">
                <el-form :label-position="labelPosition" label-width="120px" :model="dateInfo"
                         ref="dateInfo"  style="margin: 10px 0 10px 0">
                    <el-form-item label="开始日期：" prop="startDate" style="text-align:right; margin-right: 10px;" :rules="[
                    	{required: true, message: '开始日期不能为空'}
                    	]">
                        <el-date-picker v-model="dateInfo.startDate" :editable=false :clearable=false type="date" placeholder="请输入日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="结束日期：" prop="endDate" style="text-align:right; margin-right: 10px;" :rules="[
                    	{required: true, message: '结束日期不能为空'}
                    	]">
                        <el-date-picker v-model="dateInfo.endDate" :editable=false :clearable=false type="date" placeholder="请输入日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item style="text-align:right; margin-right: 10px;">
                        <el-button type="primary" @click="submitForm('dateInfo')">查询</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <el-table :data="tableData" stripe border style="width: 100%" align="left" header-align="left">
                    <el-table-column type="index" width="40"></el-table-column>
                    <el-table-column type="expand">
                        <template slot-scope="props">
                            <p>房屋面积: {{ props.row.roomSize }}平方米</p>
                            <p>报价: {{ props.row.price }}元</p>
                            <p>记录时间: {{ props.row.time }}</p>
                        </template>
                    </el-table-column>
                    <el-table-column prop="title" label="名称"></el-table-column>
                    <el-table-column prop="phone" label="电话" width="110"></el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </element>
</template>

<script>
	import remote from "@/interface/remote";
	import formatDate from "@/util/date";
	export default {
		data() {
			let cDate = new Date();
			return {
				labelPosition: 'right',
                dateInfo: {
					startDate: cDate,
					endDate: cDate,
                },
				tableData: []
			};
		},
        methods: {
			submitForm(formName) {
				let _this = this;
				this.$refs[formName].validate((valid) => {
					if (valid) {
						remote({
							url: "/customerinfo/list",
							data: {
								startDate: _this.dateInfo.startDate,
								endDate: _this.dateInfo.endDate
							},
							method: 'get'
						}).then(function (d) {
							if (d.data) {
								_this.tableData = d.data;
								_this.tableData.forEach(function (item) {
                                    item.title = item.name + item.nameType;
                                    item.time = formatDate(new Date(item.time), "yyyy-MM-dd hh:mm:ss");
								});
                            } else {
								_this.$message.error('获取数据失败！');
                            }
						}).catch(function (res) {
							console.info(res);
						});
					} else {
						console.log('error submit!!');
						return false;
					}
				});
            }
        }
	};
</script>

<style>

</style>


