import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '@/components/HelloWorld';
import Evaluate from '@/components/Evaluate';
import List from '@/components/List';

Vue.use(Router);

export default new Router({
	routes: [
		{
			path: '/',
			name: 'HelloWorld',
			component: HelloWorld
		},
		{
			path: '/evaluate',
			name: 'Evaluate',
			component: Evaluate
		},
		{
			path: '/list',
			name: 'List',
			component: List
		}
	]
});



