<template>
    <element>
        <el-row>
            <el-col :span="24">
                <el-carousel height="150px">
                    <el-carousel-item>
                        <img src="../assets/1.jpg"/>
                    </el-carousel-item>
                    <el-carousel-item>
                        <img src="../assets/2.jpg"/>
                    </el-carousel-item>
                    <el-carousel-item>
                        <img src="../assets/3.jpg"/>
                    </el-carousel-item>
                </el-carousel>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <div style="margin: 20px;"></div>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <el-form :label-position="labelPosition" label-width="80px" :model="userInfo"
                         ref="userInfo" style="margin: 0 10px 0 10px">
                    <el-form-item label="称呼">
                        <el-col :span="8">
                            <el-input v-model="userInfo.name"></el-input>
                        </el-col>
                        <el-col :span="16">
                            <el-radio v-model="userInfo.nameType" label="先生">先生</el-radio>
                            <el-radio v-model="userInfo.nameType" label="女士">女士</el-radio>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="手机号" prop="phone" :rules="[
                    	{required: true, message: '手机号不能为空'},
                    	{type: 'number', message: '手机号必须为数字值'}
                    	]">
                        <el-input v-model.number="userInfo.phone"></el-input>
                    </el-form-item>
                    <el-form-item label="房屋面积" prop="roomSize" :rules="[
                    	{required: true, message: '房屋面积不能为空'},
                    	{type: 'number', message: '房屋面积必须为数字值'}
                    	]">
                        <el-input v-model.number="userInfo.roomSize">
                            <template slot="append">平方米</template>
                        </el-input>
                    </el-form-item>
                    <el-form-item style="text-align:right">
                        <el-button type="primary" @click="submitForm('userInfo')">报价</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-dialog title="报价信息" :visible.sync="dialogTableVisible" width="80%" :close-on-click-modal="false">
            <i class="el-icon-document"/>{{userInfo.price}}元钱！
        </el-dialog>
    </element>
</template>

<script>
	import ElRow from "element-ui/packages/row/src/row";
	import ElCol from "element-ui/packages/col/src/col";

	import remote from "@/interface/remote";

	export default {
		components: {
			ElCol,
			ElRow
		},
		name: '',
		data: function () {
			return {
				labelPosition: 'right',
				userInfo: {
					name: '',
					nameType: '先生',
					phone: '',
					roomSize: '',
					price: 0
				},
				dialogTableVisible: false
			}
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						const loading = this.$loading({
							lock: true,
							text: '计算中。。。',
							spinner: 'el-icon-loading',
							background: 'rgba(0, 0, 0, 0.7)'
						});
						this.userInfo.price = this.userInfo.roomSize * 100;
						setTimeout(() => {
							loading.close();
							this.dialogTableVisible = true;
						}, 2000);
						remote({
							url: "/customerinfo/add",
							data: {
								name: this.userInfo.name,
								nameType: this.userInfo.nameType,
								phone: this.userInfo.phone,
								roomSize: this.userInfo.roomSize,
								price: this.userInfo.price
							},
							method: 'get'
						}).then(function (d) {

						}).catch(function (res) {

						});
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
		}
	};
</script>

<style lang="less">
    img {
        width: 100%;
    }

    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 150px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
</style>


